.list {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
}

.block {
	width: 400px;
	min-height: 150px;
	border: 2px solid white;
	border-radius: 15px;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-between;
	font-size: 20px;
	padding: 15px;
	margin: 10px;
	opacity: 1;
}

.input {
	width: 100%;
	height: 75px;
	display: inline-flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: nowrap;
	position: relative;
}

.value {
	width: 20%;
	text-align: right;
	color: orange;
}

input[type="range"] {
	width: 80%;
}

.upperBound, .lowerBound {
	width: 40%;
	position: absolute;
	top: 0;
	opacity: .5;
}
.lowerBound {
	text-align: left;
}

.upperBound {
	text-align: right;
	right: 20%;
}

.active {
	opacity: .5;
}

.name {
	width: 100%;
}

.stats {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.stats > p:hover, .value:hover {
	transform: scale(1.15);
}

.btn {
	color: orange;
	position: relative;
}

.btn::after {
	content: "";
	width: 0px;
	height: 2px;

	background-color: orange;

	position: absolute;
	left: 50%;
	bottom: -30%;

	transform: translate(-50%);
	transition: width .3s ease-in-out;
}

.btn:hover::after {
	width: 100%;
}
